<template>
	<div>
		<div class="search" >
		<input class="search-input" v-model="keywords" type="text" placeholder="输入城市名或拼音">
	</div>
	<div class="search-content" ref="search" v-show="keywords">
		<ul>
			<li class="search-item border-bottom" 
					v-for="item of list" 
					:key="item.id"
					@click="handleCityClick(item.name)"
					>{{ item.name }}</li>
			<li class="search-item border-bottom" v-show="hasNodata">没有找到匹配的数据</li>
		</ul>
	</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapMutations} from 'vuex'
export default {
	name: 'CitySearch',
	props:{
		cities: Object
	},
	data () {
		return {
			keywords: '',
			list: [],
			timer: null
		}
	},
	watch: {
		keywords () {
			if (this.timer) {
				clearTimeout(this.timer)
			}
			if(!this.keywords) {
				this.list = []
				return
			}
			this.timer = setTimeout(() => {
				const result = []
				for (let i in this.cities){
					this.cities[i].forEach((value) => {
						if(value.spell.indexOf(this.keywords) > -1 ||
							 value.name.indexOf(this.keywords) > -1){
							result.push(value)
						}
					})
				}
				this.list = result
			},100)
		}
	},
	methods: {
		handleCityClick (city) {
			this.changeCity(city)
			this.$router.push('/')
		},
		...mapMutations(['changeCity'])
	},
	mounted (){
		this.scroll = new BScroll(this.$refs.search,{
			click: true
		})
	},
	computed: {
		hasNodata () {
			return !this.list.length
		}
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/varibles.styl'
	.search
		height: .72rem
		background $bgColor
		padding:0 .1rem
		.search-input
			box-sizing:border-box
			height:.62rem
			width:100%
			padding:0 .1rem
			line-height:.62rem
			text-align:center
		  border-radius:.06rem
			color: #666
	.search-content
		z-index:1
		overflow:hidden
		position:absolute
		top: 1.58rem
		left:0
		right:0
		bottom:0
		background:#eee
		.search-item
			line-height: .62rem
			padding-left: .2rem
			color: #666
			background: #fff
</style>


